}

.Intro{
  animation: fade 3s ;
  /* height: 180px; */
}
.myImage{
  border: 5px solid  #173d63;
  filter: contrast(120%) brightness(130%) ;
  animation: borderColorChangeofImage ease 1s ;
  transform: rotate(-03deg);
  border-radius:120px ;
  margin: 90px auto auto;
  height: 220px;
  width: 220px;
}


.heading{
  margin: 50px 20px 32px;
  font-size: 48px;
  font-family: Arial, Helvetica, sans-serif;
}


.paragraph{
  /* animation: fade ease 1s ; */
font-family: Arial, Helvetica, sans-serif;
