  width: 150px;
  background-color: #173d63;
}


.resumetxt{
  color: whitesmoke;
  text-decoration: none;
}

#contact{
  text-align: center;
margin: 300px auto auto;
  text-align: center;  
}
.c1{
  
  height: 100px;
  justify-content: right;
  width:100%;
  /* margin: 100px; */
