  font-size: 24px; 
 color: #1d1c1c;
}
@keyframes fade {
  from {
    /* transform: translateY(0); */
    opacity: 0;
  }
  to {
    /* transform: translateY(-2rem); */

   opacity: 1;}
}

@keyframes borderColorChangeofImage {
  0% {
    transform: translateY(0);
    opacity: 0;
  border: 5px solid  #2b2928;
  }
  25% {
    opacity: 0.25;
    border: 5px solid  #3c3f63 ;
    }
    
  50% {
opacity: 0.5;
  border: 5px solid  #2f387e ;
  }
  75% {
   opacity: 0.75;
    border: 5px solid  #2f387e ;
    }
    100% {
      /* transform: translateY(-3rem); */
    opacity: 1;
      border: 5px solid   #173d63 ;
      }
}

